#footer {
  width: 100%;
  padding: 0;
  margin: 0;
  text-align: center;
}
#footer .social {
  font-size: 1.8rem;
  margin: 10px 30%;
}
#footer .text {
  color: #757575;
}
.underline {
  width: 50px;
  display: inline-block;
  position: relative;
  transform: translateZ(0);
  vertical-align: middle;
  backface-visibility: hidden;
  box-shadow: 0 0 1px transparent;
  overflow: hidden;
  cursor: pointer;
}
.underline:before {
  content: '';
  position: absolute;
  z-index: -1;
  height: 2px;
  bottom: 0;
  left: 51%;
  right: 51%;
  background-color: #00796B;
  transition-duration: 0.2s;
  transition-property: right, left;
  transition-timing-function: ease-out;
}
.underline:hover:before {
  left: 0;
  right: 0;
}
