@import './animation.less';
@prefixCls: medo-loading;

.@{prefixCls} {
  &-layout {
    width: 100%;
  }

  &-body {
    background-color: @primary-color;
    margin: auto;
    // backface-visibility: hidden;
    animation-name: reserse;
    animation-duration: 1.8s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
  }

  &-text {
    text-align: center;
    color: @primary-color;
  }
}

@primary-color: #00796B;@theme-color: #00796B;@tint-color: #009688;@light-color: #B2DFDB;@gray: #BDBDBD;@dark-gray: #757575;@light-gray: #e8e8e8;@black: #212121;@white: #f0f0f0;