.title {
  text-align: center;
  font-size: 2.4rem;
  font-weight: bold;
  color: @theme-color;
  z-index: 1;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  width: 100%;
  cursor: default;
}

.subTitle {
  font-size: 1rem;
  color: @dark-gray;
  margin: 0 20px;
}

@primary-color: #00796B;@theme-color: #00796B;@tint-color: #009688;@light-color: #B2DFDB;@gray: #BDBDBD;@dark-gray: #757575;@light-gray: #e8e8e8;@black: #212121;@white: #f0f0f0;